import { DeleteOutlined, TeamOutlined } from '@ant-design/icons'
import { Button, Card, Col, Form, Modal, Popconfirm, Row, Select, Space, Table, Tooltip } from 'antd'
import React, { useEffect, useState } from 'react'
import { useStoreActions, useStoreState } from '../../../store/hooks/easyPeasy'
import { SelectDistrict } from '../../select/SelectDistrict'
import { SelectThana } from '../../select/SelectThana'
const { Option } = Select;

export default function PendingListSuperAdmin(props) {

    const pendingList = useStoreState(state => state.partner.pendingList);
    const underPartnerList = useStoreState(state => state.partner.underPartnerList);
    const fetchPendingList = useStoreActions(state => state.partner.fetchPendingList);
    const fetchpartnerListByLevel = useStoreActions(state => state.partner.fetchpartnerListByLevel);
    const partnerAssign = useStoreActions(state => state.partner.partnerAssign);
    const applicantPartnerDelete = useStoreActions(state => state.partner.applicantPartnerDelete);
    const [assignForm] = Form.useForm();

    useEffect(() => {
        fetchPendingList();
        setPageTitle();
    }, [])

    const setPageTitle = () => {
        var pageTitleEle = document.getElementById('pageTitle');
        var pageTitle = 'Pending Applied'
        return pageTitleEle ?  pageTitleEle.innerHTML = pageTitle : ''      
    }

    const onchangePartnerLevel = (val) => {
        // console.log(val)
        fetchpartnerListByLevel(val)
        assignForm.setFieldsValue({
            under_partner: null,
        });
    };
    const [assignModal, setassignModal] = useState<boolean>(false)

    const onchangeDistrict = (val) => {
        //   console.log(val)
        setThanaId(null);
        setdisId(val)
    };
    const [disId, setdisId] = useState<any>(null);

    const onchangeThana = (val) => {
        //   console.log(val)
        setThanaId(val)
    };
    const [thanaId, setThanaId] = useState<any>(null);
    const [rowId, setRowID] = useState<any>(null);


    const [underPartnerid, setunderPartnerid] = useState<any>(null);

    const onchangeUnderPartnerLevel = (val) => {
        setunderPartnerid(val)
    };


    const assignFormSubmit = (value) => {
        let postsubmit: any = {};
        postsubmit.areaId = value?.thana_id?.thanaId;
        postsubmit.parentId = value?.under_partner;
        postsubmit.partnerId = rowId;
        postsubmit.partnerLevel = parseInt(value?.partner_level);
        // console.log(postsubmit);
        partnerAssign(postsubmit);
        setassignModal(false);
    };

    const deletePartner = value => {
        applicantPartnerDelete(value)
    }
    const columns = [
        {
            title: 'Applicant Name',
            dataIndex: 'applicantName',
            key: 'applicantName',
        },
        {
            title: 'Mobile No.',
            dataIndex: 'mobileNo',
            key: 'mobileNo',
        },        
        {
            title: 'Designation',
            dataIndex: 'designation',
            key: 'designation',
        },
        {
            title: 'Company Name',
            dataIndex: 'companyName',
            key: 'companyName',
        },
        {
            title: 'Company Address',
            dataIndex: 'companyAddress',
            key: 'companyAddress',
        },
        {
            title: 'District',
            dataIndex: 'zilla',
            key: 'zilla',
        },
        {
            title: 'Thana',
            dataIndex: 'thana',
            key: 'thana',
        },
        {
            title: 'Assign',
            render: (text: any, record: any, index) => (
                <Space size="middle">
                    <Tooltip title="Assign Partner">
                        <Button type="primary" icon={<TeamOutlined />} onClick={() => { (setassignModal(true)); setRowID(record?.partnerId) }}>Assign</Button>
                    </Tooltip>
                </Space>
            ),
        },
        {
            title: 'Action',
            render: (text: any, record: any, index) => (
                <Space size="middle">

                    <Popconfirm
                        title="Are you sure to delete this?"
                        okText="Yes"
                        cancelText="No"
                        onConfirm={() => deletePartner(record?.partnerId)}
                    >
                        <Tooltip title="Delete">
                            <Button danger icon={<DeleteOutlined />} />
                        </Tooltip>
                    </Popconfirm>

                </Space>
            ),
        },
    ]

    return (
        <>
            <Card title="Applied Pending List" >
                <Table bordered={true} dataSource={pendingList} columns={columns} />
                <Modal
                    title="Assign Partner"
                    centered
                    visible={assignModal}
                    onOk={() => (setassignModal(false))}
                    onCancel={() => (setassignModal(false))}
                    cancelButtonProps={{ style: { display: 'none' } }}
                    okButtonProps={{ style: { display: 'none' } }}
                >
                    <Form
                        layout="vertical"
                        onFinish={assignFormSubmit}
                        id="basic-info"
                        form={assignForm}
                    >
                        <Row gutter={8}>
                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="partner_level"
                                    label="Partner Level:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select level" },
                                    ]}
                                >
                                    <Select placeholder="Select Under Partner" allowClear onChange={onchangePartnerLevel}>
                                        <Option value="2">PIP</Option>
                                        <Option value="3">APIP</Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={1} xl={1}></Col>

                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="under_partner"
                                    label="Under Partner:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select under partner" },
                                    ]}
                                >
                                    <Select placeholder="Select Under Partner" allowClear options={underPartnerList} value={underPartnerid} onChange={onchangeUnderPartnerLevel}>

                                    </Select>
                                </Form.Item>
                            </Col>

                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="district_id"
                                    label="District:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select your district" },
                                    ]}
                                >
                                    <SelectDistrict onChange={(val) => onchangeDistrict(val)} selected={disId}

                                    />
                                </Form.Item>
                            </Col>
                            <Col xs={24} sm={24} md={24} lg={1} xl={1}></Col>

                            <Col xs={24} sm={24} md={24} lg={11} xl={11}>
                                <Form.Item
                                    name="thana_id"
                                    label="Area:"
                                    className="title-Text"
                                    rules={[
                                        { required: true, message: "Please select your area" },
                                    ]}
                                >
                                    <SelectThana onChange={(val) => onchangeThana(val)} selected={thanaId}

                                    />
                                </Form.Item>

                            </Col>
                        </Row>
                        <Space size="small" style={{ float: "right" }}>
                            <Button type="primary" htmlType="submit" >
                                Assign
                                </Button>
                        </Space>
                        <br />
                    </Form>

                </Modal>
            </Card>
            
        </>
    )
}
